<template>
  <main class="p_one">
    <section>
      <span class="char h1">{{ project.project.title[0] }}</span>
      <div class="cm mcm">
      <div class="p_title">
        <h2>{{ project.project.title }}</h2>
        <a :href="project.project.projectLink" target="_blank" class="www">{{ project.project.projectLinkTitle }}</a>
      </div>
      </div>
      <div class="p_video">
        <span class="bordered">Simikov photographer</span>
        <div class="p_screen">
          <video class="" muted playsinline loop="true" autoplay="true" :src="project.project.videoLink">
          </video>
        </div>
      </div>
      <div class="cm mcm">
      <div class="p_description">
        <p>{{ project.project.description }}</p>
      </div>
      <div class="p_links">
        <div><a target="_blank" :href="project.project.behanceLink">{{ project.project.title }}  on Behance</a></div>
        <div>Also visit our <a href="https://www.facebook.com/fiddle.digital" target="_blank">Facebook</a> and <a href="https://dribbble.com/Fiddle-digital" target="_blank">Dribbble</a>.</div>
      </div>
      </div>
    </section>
  </main>
</template>

<script>
  import { DateTime } from 'luxon'
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'project',
    computed: {
      created () {
        if (this.project.project.createdOn) {
          return DateTime
            .fromISO(this.project.project.createdOn)
            .toLocaleString({ weekday: 'long', month: 'long', day: '2-digit', year: 'numeric' })
        }
        return ''
      },

      ...mapGetters({
        project: 'project'
      })
    },

    methods: {
      ...mapActions({
        load: 'project'
      })
    },

    beforeRouteEnter (to, from, next) {
      next(vm => {
        vm.load(vm.$route.params.id)
      })
    }
  }
</script>

